@charset "UTF-8";

.container.footer_container {
  flex-direction: row;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0E0E0E;
  overflow: hidden;
}

.footer_left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 37%;
  border-right: 1px solid #202020;
  color: #fff;
}

.footer_left_title {
  font-size: 34px;
  font-weight: 500;
  line-height: 44px;
  margin: 120px 0 30px;
}

.footer_left_text {
  font-size: 16px;
  font-weight: 400;
  max-width: 240px;
}

.footer_left_list {
  padding: 75px 0 80px;
}

.footer_left_list_item {
  color: #969696;
  margin: 0 0 10px;
}

.footer_left_list_link {
  text-decoration: none;
  color: #fff;
}

.footer_right {
  display: flex;
  justify-content: left;
  align-items: flex-start;
  flex-direction: column;
  margin: 125px 0 0 100px;
  width: 63%;
  position: relative;
}

.footer_right_title {
  font-family: Inter;
  font-size: 15px;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: #969696;
}

.footer_right_list_links {
  display: flex;
  width: 100%;
  max-height: 100px;
  margin: 130px 0 175px;
  z-index: 2;
}

.footer_right_list_link {
  text-decoration: none;
  color: #fff;
  position: relative;
}

.footer_right_list_link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  transform: scale(0);
  background-color: #fff;
  bottom: 0;
  left: 0;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.footer_right_list_link:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.footer_right_list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: inherit;
  gap: 10px;
}

.footer_right_circle {
  position: absolute;
  width: 700px;
  height: 700px;
  border-radius: 50%;
  background-color: #202020;
  z-index: 1;
  right: -25%;
  bottom: -40%;
}

.footer_right_form {
  width: 75%;
  z-index: 2;
  position: relative;
}

.footer_right_subscribe {
  width: 100%;
  margin: 35px 0 0;
  color: #fff;
  border: 0;
  border-bottom: 1px solid #fff;
  background: transparent;
  height: 50px;
  font-size: 20px;
}

.footer_right_btn {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 50px;
  width: 80px;
  border: 0;
  background: transparent;
  cursor: pointer;
  transition: opacity 0.35s ease-out, width 0.5s ease-out, background 0.25s ease-out;
  color: #fff;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
}

.footer_right_btn::after {
  content: "●";
  font-size: 13px;
  position: absolute;
  top: 47%;
  transform: translateY(-50%);
  right: calc(50% - 40px);
}

.footer_right_btn.footer_btn_active {
  background-color: #1c201c;
  width: 100%;
  font-weight: 500;
}

.footer_right_btn.footer_btn_active::after {
  right: calc(50% - 115px);
}

.footer_right ::placeholder {
  color: #fff;
  margin: 5px 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  transform: translateY(0px);
  transition: all 0.3s ease-in-out;
}

.footer_right input[type=text]:hover::placeholder,
.footer_right input[type=text]:focus::placeholder {
  color: #969696;
  font-size: 14px;
  transform: translateY(-20px);
}